import React from 'react';

export default class PhaserJS extends React.Component {
  constructor(props) {
    super(props)
  }

  componentDidMount() {

  }

  render() {
    return (
      <div className="container PhaserJS">
        <h2>How to create an Insertion Sort Animation in PhaserJS while Programming Insertion Sort Algorithm in Javascript</h2>

        <video width="860" height="640" controls>
          <source src="video.mp4" type="video/mp4"/>
          Your browser does not support the video tag.
        </video>
        
        <div className="heading">
          <h3>Create the code for Algorithm</h3>
        </div>
        <p>Insertion Sort is an algorithm which moves the unsorted elements from a section of the entire array to a sorted section. 
          Insertion Sort runs passes of the algorithms by sorting one element at a time deciding its correct position within the sorted section.
        </p>
        <dl className="datalist">
          <dt>1st Pass</dt>
          <dd>In the first pass, the elements are selected for a comparison. </dd>
          <img src="insertion-sort1.png" alt="Insertion Sort 1st Pass" title="Insertion Sort 1st Pass" />
          <img width="765" src="code1.png" alt="Insertion Sort 1st Pass (Code)" title="Insertion Sort 1st Pass (Code)" />
          <dt>2nd Pass</dt>
          <dd>In the second pass, the second occuring element in the order and the first occuring element in the order are compared against. Then if the second element is larger than the first element 
            the order is retained and moved on to the next pass, otherwise the second element is inserted before the first element to form the first sorted portion of the array keeping the first element in the same position.
          </dd>
          <img src="insertion-sort2.png" alt="Insertion Sort 2nd Pass" title="Insertion Sort 2nd Pass" />
          <img width="765" src="code2.png" alt="Insertion Sort 1st Pass (Code)" title="Insertion Sort 1st Pass (Code)" />
          <dt>3rd Pass</dt>
          <dd>In the third pass, the next element in the order is selected to compare against the sorted initial portion of the array. Then it repeats the process of the 2nd pass to move the next element to the sorted portion.
          </dd>
          <img src="insertion-sort3.png" alt="Insertion Sort 3rd Pass" title="Insertion Sort 3rd Pass" />
          <dt>4th Pass</dt>
          <dd>In the fourth pass, the next element in the order is selected to compare against the sorted initial portion of the array. Then it moves the element to the sorted portion if the element is smaller than the largest element of the array.
          </dd>
          <img src="insertion-sort4.png" alt="Insertion Sort 4th Pass" title="Insertion Sort 4th Pass" />
          <dt>5th Pass</dt>
          <dd>In the fifth pass, the next element in the order is selected to compare against the sorted initial portion of the array. Then it moves the element to the sorted portion if the element is smaller than the largest element of the array.
          </dd>
          <img src="insertion-sort5.png" alt="Insertion Sort 5th Pass" title="Insertion Sort 5th Pass" />
        </dl>

        <div className="heading">
        <h3>How to setup PhaserJS for Programming</h3>
        </div>
        <p>You may operate PhaserJS as a Game initialisation or a Scene initialisation. In this article, I have explained using Scene initialisation. </p>
        <img width="765" src="phaserjs.png" alt="PhaserJS React (Code)" title="PhaserJS React (Code)" />

        <p>You may name a scene as the same name as the Scene Class and then can start multiple scenes which will run on the canvas. </p>

        <div className="heading">
        <h3>How the StartAnimate and EndAnimate code are written?</h3>
        </div>
        <p>The StartAnimate and EndAnimate code methods are part of the Insertion Sort Algorithm which moves the elements downwards in the video and also moves to the right respectively.
          Here is the code anippet explaining this.
        </p>
        <img width="765" src="animate.png" alt="PhaserJS StartAnimate and EndAnimate (Code)" title="PhaserJS StartAnimate and EndAnimate (Code)" />

        <div className="heading">
        <h3>What the scene class is exactly?</h3>
        </div>
        <p>The Scene class and similar classes with the same methods exist in the Game to run a loop over the Game's clocking time. 
          The Scene class is a class with `init()`, `preload()`, `create()` and `update()` methods. 
        
        </p>
        <p>See the code snippet here.</p>
        <img width="765" src="scene.png" alt="PhaserJS StartAnimate and EndAnimate (Code)" title="PhaserJS StartAnimate and EndAnimate (Code)" />
      </div>
    )
  }
}