import React from 'react';
import ReactDOM from 'react-dom';
import Projects from '../components/Projects';
import Nanodegrees_Challenges from '../components/Nanodegrees_Challenges';
import Courses_Certifications from '../components/Courses_Certifications';
import Additional_Work_Presentations from '../components/Additional_Work_Presentations';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

export default class FrontPageController extends React.Component {

  render() {
    
    return (
      <div className="container Profile">
        <Tabs>
          <TabList>
            <Tab>Projects</Tab>
            <Tab>Nanodegrees &amp; Challenges</Tab>
            <Tab>Courses &amp; Certifications</Tab>
            <Tab>Additional Work &amp; Presentations</Tab>
          </TabList>

          <TabPanel>
            <Projects tableColor="blue" />
          </TabPanel>
          <TabPanel>
            <Nanodegrees_Challenges tableColor="orange" />
          </TabPanel>
          <TabPanel>
            <Courses_Certifications tableColor="yellow" />
          </TabPanel>
          <TabPanel>
            <Additional_Work_Presentations tableColor="magenta" />
          </TabPanel>
        </Tabs>
      </div>
    );
  
  }

}